import React from 'react';
import { Accordion, Card, Container, ListGroup } from 'react-bootstrap';
import { projects } from './ProjectsSection';

const SkillsSection = () => {
  // Mapping des comptences du programme avec les cls utilises dans les projets
  const competences = [
    {
      programName: 'Raliser',
      projectSkill: 'Dvelopper',
      description: 'Dvelopper une solution informatique pour un client',
      ces: [
        'CE1.0.1 | Respecter les exigences du client',
        'CE1.0.2 | Appliquer les principes algorithmiques',
        'CE1.0.3 | Garantir la qualit du code et documentation',
        'CE1.0.4 | Choisir les ressources techniques appropries',
      ],
    },
    {
      programName: 'Optimiser',
      projectSkill: 'Optimiser',
      description: 'Proposer des applications optimises',
      ces: [
        'CE2.0.1 | Formaliser des situations complexes',
        'CE2.0.2 | Utiliser des algorithmes et structures de donnes',
        'CE2.0.3 | Appliquer des raisonnements logiques',
        'CE2.0.4 | Justifier les choix techniques',
      ],
    },
    {
      programName: 'Administrer',
      projectSkill: 'Administrer',
      description: 'Maintenir des infrastructures informatiques',
      ces: [
        'CE3.0.1 | Grer le systme d\'information',
        'CE3.0.2 | Appliquer les normes de scurit',
        'CE3.0.3 | Assurer la qualit de service',
        'CE3.0.4 | Garantir la continuit d\'activit',
      ],
    },
    {
      programName: 'Grer',
      projectSkill: 'Grer',
      description: 'Exploiter les donnes de l\'entreprise',
      ces: [
        'CE4.0.1 | Respecter les rglementations',
        'CE4.0.2 | Considrer les impacts conomiques/cologiques',
        'CE4.0.3 | S\'appuyer sur des bases mathmatiques',
        'CE4.0.4 | Assurer la qualit des donnes',
      ],
    },
    {
      programName: 'Conduire',
      projectSkill: 'Conduire',
      description: 'Satisfaire les besoins utilisateurs',
      ces: [
        'CE5.0.1 | Adopter une dmarche proactive',
        'CE5.0.2 | Respecter les rgles juridiques',
        'CE5.0.3 | Communiquer efficacement',
        'CE5.0.4 | Sensibiliser  l\'thique',
      ],
    },
    {
      programName: 'Collaborer',
      projectSkill: 'Collaborer',
      description: 'Travailler en quipe informatique',
      ces: [
        'CE6.0.1 | Intgrer une quipe pluridisciplinaire',
        'CE6.0.2 | Mettre en uvre des solutions quilibres',
        'CE6.0.3 | Respecter les contraintes juridiques',
        'CE6.0.4 | Dvelopper une communication collaborative',
      ],
    },
  ];

  return (
    <Container id="skills" className="py-5">
      <h2 className="text-center mb-4">Comptences du Programme National</h2>
      
      <Accordion >
        {competences.map((competence, index) => {
          const relatedProjects = projects.filter(project => 
            project.skills.includes(competence.projectSkill)
          );

          return (
            <Accordion.Item key={index} eventKey={index.toString()}>
              <Accordion.Header>
                <strong>{competence.programName}</strong> : {competence.description}
              </Accordion.Header>
              
              <Accordion.Body>
                <h5>Composantes Essentielles :</h5>
                <ListGroup variant="flush">
                  {competence.ces.map((ce, ceIndex) => (
                    <ListGroup.Item key={ceIndex}>{ce}</ListGroup.Item>
                  ))}
                </ListGroup>

                <h5 className="mt-4">Projets Associs :</h5>
                {relatedProjects.length > 0 ? (
                  <div className="d-flex flex-wrap gap-3">
                    {relatedProjects.map((project, pIndex) => (
                      <Card key={pIndex} style={{ width: '18rem' }}>
                        <Card.Img 
                          variant="top" 
                          src={project.image} 
                          style={{ height: '150px', objectFit: 'cover' }}
                        />
                        <Card.Body>
                          <Card.Title>{project.name}</Card.Title>
                        </Card.Body>
                      </Card>
                    ))}
                  </div>
                ) : (
                  <p>Aucun projet associ pour cette comptence</p>
                )}
              </Accordion.Body>
            </Accordion.Item>
          );
        })}
      </Accordion>
    </Container>
  );
};

export default SkillsSection;