import React from 'react';
import { Tabs, Tab, Container } from 'react-bootstrap';
import { FaGraduationCap, FaBriefcase } from 'react-icons/fa'; // Icnes pour la timeline
import './Timeline.css'; // Custom CSS for the timeline

const ExperiencesSection = () => {
  // Donnes pour le parcours acadmique
  const academicData = [
    {
      date: '2022 - 2025',
      title: 'BUT Informatique',
      subtitle: 'IUT de Blagnac',
      description: 'Formation en dveloppement logiciel, gestion de projet et bases de donnes.',
    },
    {
      date: '2022',
      title: 'Baccalaurat Gnral',
      subtitle: 'Lyce Georges Leygues, Villeneuve-sur-Lot',
      description: 'Spcialit Numrique et Sciences Informatiques et Mathmatiques.',
    },
  ];

  // Donnes pour les expriences professionnelles
  const professionalData = [
    {
      date: '2023 - Prsent',
      title: 'Dveloppeur en Alternance',
      subtitle: 'Nomattitude',
      description: 'Participation au dveloppement de solutions logicielles adaptes aux besoins des clients avec C#, Node.js et React.js.',
    },
    {
      date: 't 2022',
      title: 'Rceptionniste',
      subtitle: 'Leclerc Villeneuve-sur-Lot',
      description: 'Rception des palettes des camions, vrification des marchandises et gestion des stocks.',
    },
  ];

  return (
    <section id="experiences" className="py-5 bg-light">
      <Container>
        <h2 className="text-center mb-5">Expriences</h2>
        <Tabs defaultActiveKey="academic" id="experience-tabs" className="mb-4 justify-content-center">
          {/* Onglet Parcours acadmique */}
          <Tab eventKey="academic" title="Parcours acadmique">
            <div className="timeline">
              {academicData.map((item, index) => (
                <div key={index} className="timeline-item">
                  <div className="timeline-icon">
                    <FaGraduationCap />
                  </div>
                  <div className="timeline-content">
                    <h3>{item.title}</h3>
                    <h4>{item.subtitle}</h4>
                    <p>{item.description}</p>
                    <span className="timeline-date">{item.date}</span>
                  </div>
                </div>
              ))}
            </div>
          </Tab>

          {/* Onglet Expriences professionnelles */}
          <Tab eventKey="professional" title="Expriences professionnelles">
            <div className="timeline">
              {professionalData.map((item, index) => (
                <div key={index} className="timeline-item">
                  <div className="timeline-icon">
                    <FaBriefcase />
                  </div>
                  <div className="timeline-content">
                    <h3>{item.title}</h3>
                    <h4>{item.subtitle}</h4>
                    <p>{item.description}</p>
                    <span className="timeline-date">{item.date}</span>
                  </div>
                </div>
              ))}
            </div>
          </Tab>
        </Tabs>
      </Container>
    </section>
  );
};

export default ExperiencesSection;
