import React from 'react';
import { Typography, Box } from '@mui/material';

const AboutSection = () => {
  return (
    <Box id="about" sx={{ padding: '40px 0', textAlign: 'justify' }}>
      <Typography variant="h4" gutterBottom> propos de moi</Typography>
      <Typography variant="body1">
        <Box style={{ marginBottom: '20px' }}>
          Je suis un <b>dveloppeur passionn</b> avec une curiosit insatiable pour les nouvelles technologies et une crativit nourrie par mes diverses passions : les <b>jeux vido</b>, le <b>cinma</b>, la <b>musique</b>, et le <b>sport</b>. Ces centres d'intrt m'inspirent et m'aident  adopter des approches innovantes dans mes projets.
        </Box>
        <Box style={{ marginBottom: '20px' }}>
          Actuellement en <b>3 anne de BUT Informatique en alternance</b>, je combine une solide formation acadmique avec une exprience pratique acquise en milieu professionnel. Cette double comptence me permet de travailler efficacement sur des projets varis, allant du dveloppement logiciel  la gestion de donnes en passant par loptimisation de systmes.
        </Box>
        <Box style={{ marginBottom: '20px' }}>
          Dans mon temps libre, je continue d'explorer mes passions, que ce soit en dveloppant des <b>projets personnels</b>, en dcouvrant de nouvelles musiques avant-gardistes, ou en analysant les mcaniques des jeux vido pour enrichir ma comprhension du design interactif.
        </Box>
        <Box style={{ marginBottom: '20px' }}>
          Que ce soit pour collaborer sur un projet, changer sur des ides, ou partager des expriences, je suis toujours  la recherche de nouvelles opportunits pour apprendre et grandir.
        </Box>
      </Typography>
    </Box>
  );
};

export default AboutSection;